import React from "react";
import { useState } from "react";
import Dropdown from 'react-bootstrap/Dropdown';
import DropdownButton from 'react-bootstrap/DropdownButton';
import {FiMenu} from 'react-icons/fi';
import {AiOutlineClose} from 'react-icons/ai'

import './styled.css'

const MenuMob =()=>{

    const [open,setOpen]=useState(false);

    const iconopen=<FiMenu className="hamburger" size='40px'
                    color="white" onClick={()=>setOpen(!open)}/>

    const iconclose=<AiOutlineClose className="hamburger" size='40px'
                    color="white" onClick={()=>setOpen(!open)}/>               


    return(
        <>
        {open ? iconclose : iconopen}
        {open &&<ul className="ul-menu-mob">
                        <li><a href="/"> Inicio</a></li>
                        <li><DropdownButton id="dropdown-basic-button" title="Sries Escolares" >
                            <Dropdown.Item href="/Maternal">Maternal</Dropdown.Item>
                            <Dropdown.Item href="/Fundamental1">Fundamental 1</Dropdown.Item>
                            <Dropdown.Item href="/Fundamental2">Fundamental 2</Dropdown.Item>
                            <Dropdown.Item href="/Hibrido">Ensino Hbrido</Dropdown.Item>
                            </DropdownButton></li>
                        <li><DropdownButton id="dropdown-basic-button" title="Portal Galileu" >
                            <Dropdown.Item href="https://portalaluno.app/auth/login" target="_blank">Portal do Aluno</Dropdown.Item>
                            <Dropdown.Item href="https://appgalileu.com.br/professor" target="_blank">Portal do Professor</Dropdown.Item>
                            </DropdownButton></li>
                        <li><a href="/Noticias">Noticias e Eventos</a></li>
                        <li><a href="/Contato">Contato</a></li>
                        <li><a href="/QuemSomos">Quem Somos</a></li>
                    </ul>}
            
        </>           
    )
}
export default MenuMob